// Import dependencies
import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

class DateCard extends Component {
  render () {
    const {
      // Props passed down
      heading = '',
      date = '',
      classes = '',
      // Props coming from state
      baseDateIsInThePast = '',
      baseDateIsInTheFuture = '',
      targetDateIsInThePast = '',
      targetDateIsInTheFuture = '',
    } = this.props;
    return (
      <div className={`date${classes ? ' ' + classes : ''}${baseDateIsInThePast ? ' base-date-is-in-the-past' : ''}${baseDateIsInTheFuture ? ' base-date-is-in-the-future' : ''}${targetDateIsInThePast ? ' target-date-is-in-the-past' : ''}${targetDateIsInTheFuture ? ' target-date-is-in-the-future' : ''}`}>
        <h3 className="date__heading m-b-20">
          {heading}
        </h3>
        <div className="date__moment">
          {date}
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({main}) => {
  return {
    baseDateIsInThePast: main.baseDateIsInThePast,
    baseDateIsInTheFuture: main.baseDateIsInTheFuture,
    targetDateIsInThePast: main.targetDateIsInThePast,
    targetDateIsInTheFuture: main.targetDateIsInTheFuture,
  }
}

const DateCardConnect = connect(
  mapStateToProps,
)(DateCard);

DateCard.propTypes = {
  // Props passed down
  heading: PropTypes.string,
  date: PropTypes.string,
  classes: PropTypes.string,
  // Props coming from state
  baseDateIsInThePast: PropTypes.bool,
  baseDateIsInTheFuture: PropTypes.bool,
  targetDateIsInThePast: PropTypes.bool,
  targetDateIsInTheFuture: PropTypes.bool,
};

export default DateCardConnect;
